.tribute_app {
    position: relative;
    width: 32rem;
    /* height: 64rem; */
    margin: auto;
    border: 16px #000 solid;
    border-top-width: 60px;
    border-bottom-width: 60px;
    border-radius: 36px;
    box-sizing: inherit;
  }
  .tribute_app:before {
    content: '';
    display: block;
    width: 60px;
    height: 5px;
    position: absolute;
    /* позиция линии относительно .smartphone */
    top: -30px;
    left: 50%;
    transform: translate(-50%, -50%);
    /* выравнивание по центру */
    background: var(--tributeAfterBefore);
    border-radius: 10px;
  }
  
  .tribute_app:after {
    content: '';
    display: block;
    width: 35px;
    height: 35px;
    position: absolute;
    left: 50%;
    bottom: -65px;
    transform: translate(-50%, -50%);
    background: var(--tributeAfterBefore);
    border-radius: 50%;
  }
  .content {
    padding: 1px;
    width: 32rem;
    height: 64rem;
    overflow-y: scroll;
    scrollbar-width: none;
    background-color: var( --colorBackgroundAll);
