:root {
    box-sizing: border-box;
}
body {
    background-color: rgb(66, 163, 170);
    font-family: Helvetica, Arial, sans-serif;
}
*,
 ::before,
 ::after {
    box-sizing: inherit;
    
}
.row {
    margin-left: -0.75em;
    margin-right: -0.75em;
    max-height: 40rem;
    overflow-y: scroll;

}

.row::-webkit-scrollbar {
    width: 1px;
  }

.row::after {
    content: " ";
    display: block;
    clear: both;
}

.column50 {
    width: 45%;
}
[class*="column50"] {
    float: left;
    padding: -1em 0.75em;
    margin-top: 0;
    margin-left: 1.2em;
}

header {
    padding: 1em 1.5em;
    color: #fff;
    background-color: #0072b0;
    border-radius: .5em;
    margin-bottom: 1.5em;
}

