.container {
    max-height: 64rem;
    margin: 0 auto;
}
.container h2 {
    background-color: rgba(252, 240, 240, 0.904);
    box-shadow: 0 8px 15px 0 rgba(0,0,0,0.3), 0 5px 21px 0 rgba(0,0,0,0.18);
padding: 1em;
border-radius: 0.5em;
}

.media {
     float: left;
     margin: 0 1.5em 1.5em 0;
     /* width: calc(50% - 4.5em); */
     height: 14em;
    padding: 1.4em;
    background-color: #3f989f2e;
    border-radius: 0.5em;
    box-shadow: 0 8px 15px 0 rgba(0,0,0,0.3), 0 5px 21px 0 rgba(0,0,0,0.18);
}

.media:hover{
    cursor: pointer;
    transform: translate(15px, -5px);
  }
  .clearfix {
    max-width: 64rem;
}
.clearfix::before,
.clearfix::after {
    display: table;
    content: " ";
}

.clearfix::after {
    clear: both;
}

.mediaImage {
    /* float: left; */
    /* margin-right: 0.5em; */
    padding-bottom: 0.5em;
}

.mediaBody {
    /* overflow: auto; */
    margin-top: 0;
}

.mediaBody h5 {
    margin-top: 1px;
}
.mediaBody p {
    padding-bottom: 1em;
    margin-bottom: 1em;
}
